import React from 'react';
import styles from "./Footer.module.css";

const Footer = () => {
  const scrollToTop = () => {
    window.scrollTo(0, 0)
  }
  return (
    <footer>
      <div className={styles.footerWrapper}>
        <button className={`btn btn-danger ${styles.top}`} onClick={scrollToTop}>Voltar para o topo</button>
        {/* //Na linha seguinte dever√° ser feito um teste se a aplica√ß√£o
        // est√° em dark mode e dever√° utilizar a class navbar-dark bg-dark ou navbar-light bg-light  */}
        <div className={`navbar-light bg-light} ${styles.footer}`}>
          <div className="container">
            <div className={`row`}>
              <div className="col-sm-12 col-lg-6">
                {/* //Na linha seguinte dever√° ser feito um teste se a aplica√ß√£o
                // est√° em dark mode e dever√° utilizar o css correto */}
                <img className={`${styles.dhLogo}`} src="/images/DH.png" alt='DH-logo' />
              </div>
              <div className={`col-sm-12 col-lg-6 ${styles.icons}`}>
                <img src="/images/ico-facebook.png" alt="√≠cone do facebook" className={styles.icon} />
                <img src="/images/ico-instagram.png" alt="√≠cone do instagram" className={styles.icon} />
                <img src="/images/ico-whatsapp.png" alt="√≠cone do whatsapp" className={styles.icon} />
                <img src="/images/ico-tiktok.png" alt="√≠cone do tiktok" className={styles.icon} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </footer >
  )
}

export default Footer